.markdown-view {
  line-height: 1.5;

  .markdown-lang {
    position: absolute;
    top: 10px;
    right: 10px;
    color: #d9d9d9;
    font-size: 12px;
    line-height: 12px;
  }

  pre {
    position: relative;
    padding: 10px;
    background: #333;
    line-height: 1.5;

    &::-webkit-scrollbar {
      width: 8px;
      height: 8px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: hsla(220, 4%, 58%, 0.5);
      border-radius: 6px;
    }

    &::-webkit-scrollbar-track {
      background-color: rgba(0, 0, 0, 0);
    }
  }

  a {
    color: $primary;
    text-decoration: none;

    &:hover {
      color: $active;
      border-bottom: 1px dashed $active;
    }
  }

  pre > code {
    max-width: 100%;
  }

  li > code,
  p > code {
    max-width: 100%;
    padding: 0 8px;
    margin: 0 4px;
    background: rgba($primary, 0.1);
    border-radius: 4px;
  }

  blockquote,
  ol,
  ul,
  p,
  table,
  h2,
  h3,
  h4 {
    margin-bottom: 12px;
  }

  h1 {
    margin-top: 24px;
    margin-bottom: 24px;
  }

  blockquote,
  p,
  ol,
  ul,
  table {
    & + h4,
    & + h3,
    & + h2,
    & + h1 {
      margin-top: 18px;
    }
  }

  table {
    thead tr {
      font-weight: 600;
    }
  }

  blockquote {
    position: relative;
    padding-left: 20px;
    background: rgba($primary, 0.1);
    padding: 10px;
    border-left: 3px solid $primary;

    p {
      &:last-of-type {
        margin-bottom: 0;
      }
    }

    & + ol,
    & + ul {
      margin-top: 12px;
    }
  }

  ol {
    counter-reset: sectioncounter;

    li {
      padding-left: 20px;
      position: relative;
      list-style: none;

      &:before {
        position: absolute;
        top: 0;
        left: 0;
        font-size: inherit;
        content: counter(sectioncounter) '.';
        counter-increment: sectioncounter;
        font-size: 12px;
        color: $black;
      }
    }
  }

  h1 {
    font-size: 24px;
    text-align: center;
  }

  h2 {
    font-size: 18px;
  }

  h3 {
    font-size: 16px;
  }

  h4 {
    font-size: 14px;
  }

  hr {
    margin: 20px 0;
    border: none;
    height: 1px;
    background: linear-gradient(90deg, $green, $primary);
    opacity: 0.2;
  }

  h1,
  h2,
  h3,
  h4 {
    a {
      color: $black;
    }
  }

  img {
    max-width: 100%;
    margin: 12px auto;
  }

  em {
    font-style: normal;
    font-weight: 600;
    color: $active;
  }

  .hljs-comment {
    font-style: normal;
  }

  p {
    margin-bottom: 20px;
    font-size: 14px;
  }
}